import axios from 'axios';
import React from 'react'
import { useState, useEffect } from 'react';
import avatar from '../avatar.jpeg'
import { Link } from 'react-router-dom';
export default function Home() {
    const [trendingMovies, setTrendingMovies] = useState([]);
    const [trendingTv, setTrendingTv] = useState([]);
    const [trendingPeople, setTrendingPeople] = useState([]);

    //Get Trending Function
    async function getTrending(mediaType, callBack) {
        let { data } = await axios.get(`https://api.themoviedb.org/3/trending/${mediaType}/week?api_key=eba8b9a7199efdcb0ca1f96879b83c44`)
        callBack(data.results.slice(0, 10));
    }

    //Call Get Trending Function
    useEffect(() => {
        getTrending('movie', setTrendingMovies);
        getTrending('tv', setTrendingTv);
        getTrending('person', setTrendingPeople);
    }, [])

    return (
        <>
            <div className="container">
                <div className="row g-4 mb-4">
                    <div className="col-md-4 d-flex align-items-center">
                        <div className="trending-header position-relative ">
                            <h2 className='py-3'>Trending <br /> Movies <br /> to Watch Now</h2>
                            <p className='text-muted'>Most Watched Movies by Day</p>
                        </div>
                    </div>
                    {trendingMovies.map((movie, i) => <div key={i} className='col-md-2'>
                        <div className="movie position-relative">
                            <span className='bg-info text-white p-1 position-absolute top-0 end-0'>{movie.vote_average.toFixed(1)}</span>
                            <Link to={`/moviedetails/${movie.id}`}>
                                <img src={"https://image.tmdb.org/t/p/w500" + movie.poster_path} className='w-100' alt="" />
                                <h3 className='h6 my-2'>{movie.title}</h3>
                            </Link>
                        </div>

                    </div>)}
                </div>
                <div className="row g-4 mb-4">
                    <div className="col-md-4 d-flex align-items-center">
                        <div className="trending-header position-relative ">
                            <h2 className='py-3'>Trending <br /> TV <br /> to Watch Now</h2>
                            <p className='text-muted'>Most Watched TV by Day</p>
                        </div>
                    </div>
                    {trendingTv.map((tv, i) => <div key={i} className='col-md-2'>
                        <div className="tv position-relative" >
                            <span className='bg-info text-white p-1 position-absolute top-0 end-0'>{tv.vote_average.toFixed(1)}</span>
                            <Link to={`/tvdetails/${tv.id}`}>
                                <img src={"https://image.tmdb.org/t/p/w500" + tv.poster_path} className='w-100' alt="" />
                                <h3 className='h6 my-2'>{tv.name}</h3>
                            </Link>
                        </div>
                    </div>)}
                </div>
                <div className="row g-4">
                    <div className="col-md-4 d-flex align-items-center">
                        <div className="trending-header position-relative ">
                            <h2 className='py-3'>Trending <br /> People <br /> to Watch Now</h2>
                            <p className='text-muted'>Most Watched People by Day</p>
                        </div>
                    </div>
                    {trendingPeople.map((person, i) => <div key={i} className='col-md-2'>
                        <Link to={`/persondetails/${person.id}`}>
                            {person.profile_path === null ? <img src={avatar} className='w-100 avatar' alt="" /> :
                                <img src={"https://image.tmdb.org/t/p/w500" + person.profile_path} className='w-100 ' alt="" />
                            }
                            <h3 className='h6 my-2'>{person.name}</h3>
                        </Link>
                    </div>)}
                </div>
            </div>

        </>
    )
}
