import React from 'react'
import { useState } from 'react';
import axios from 'axios';
import { useEffect } from 'react';
import { Link } from 'react-router-dom';

export default function Movies() {
    let nums = new Array(13).fill(1).map((elem, index) => index+1);
    const [trendingMovies, setTrendingMovies] = useState([]);
    //Get Trending Function
    async function getTrending(pageNumber) {
        let { data } = await axios.get(`https://api.themoviedb.org/3/discover/movie?api_key=39ece93b8dd373dac4034384c03dbbda&language=en-US&sort_by=popularity.desc&include_adult=false&include_video=false&page=${pageNumber}&with_watch_monetization_types=flatrate`)
        setTrendingMovies(data.results);
    }
    //Call Get Trending Function
    useEffect(() => {
        getTrending(1);

    }, [])
    return (
        <>
            {trendingMovies ? <div className='container mt-5 pt-5'>
                <div className="row g-5 justify-content-center">
                    {trendingMovies.map((movie, i) => <div key={i} className='col-md-2'>
                        <Link to={`/moviedetails/${movie.id}`}>
                            <img src={"https://image.tmdb.org/t/p/w500" + movie.poster_path} className='w-100' alt="" />
                            <h3 className='h6 my-2'>{movie.title}</h3>
                        </Link>
                    </div>)}
                </div>
                <nav aria-label="...">
                    <ul className="pagination pagination-sm d-flex justify-content-center ">
                        {
                            nums.map((pageNum, i)=> <li onClick={()=> getTrending(pageNum)} key={pageNum} className="page-item my-4 bg-transparent"><a className="page-link bg-transparent text-white ">{pageNum}</a></li>)
                        }
                    </ul>
                </nav>
            </div> :
                <div className='vh-100 w-100 d-flex justify-content-center align-items-center'>
                    <i className='fas fa-spinner fa-spin fa-3x'></i>
                </div>}

        </>
    )
}
