import axios from 'axios';
import Joi from 'joi';
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';

export default function Register() {
    //navigate
    let navigate = useNavigate();
    //is loading Spinner
    const [isLoading, setIsLoading] = useState(false)
    //Validation Errors
    const [validationErrors, setValidationErrors] = useState([]);
    //registertion Error Message
    const [error, setError] = useState('');
    //User Data
    const [user, setUser] = useState({
        first_name: '',
        last_name: '',
        age: 0,
        email: '',
        password: ''
    });

    //Get user Data From Form Inputs
    function getUserData(e) {
        let myUser = { ...user };//Deep Copy
        myUser[e.target.name] = e.target.value;
        setUser(myUser);
    }

    //Submit Register Form
    async function submitRegisterForm(e) {
        //stop Browser reload on submit
        e.preventDefault();
        //show loading Spinner
        setIsLoading(true);
        //Validation Result
        let validationResult = validateRegisterForm();
        // if There Are Errors??
        if (validationResult.error) {
            //stop Loading Spinner
            setIsLoading(false);
            //Show Validation Error Details
            setValidationErrors(validationResult.error.details);
        }
        //if There is No Errors
        else {
            //post User Data To Data base
            let { data } = await axios.post('https://route-egypt-api.herokuapp.com/signup', user)
            //Registration tmam
            if (data.message === 'success') {
                //stop Loading Spinner
                setIsLoading(false);
                //Navigate to Login
                navigate('/login');
            }
            //Registration Error
            else {
                //stop Loading Spinner
                setIsLoading(false);
                //show Error
                setError(data.message);
            }
        }
    }

    //Validate Register Form
    function validateRegisterForm() {
        let scheme = Joi.object({
            first_name: Joi.string().alphanum().min(3).max(10).required(),
            last_name: Joi.string().alphanum().min(3).max(10).required(),
            age: Joi.number().min(16).max(80).required(),
            email: Joi.string().email({ minDomainSegments: 2, tlds: { allow: ['com', 'net'] } }).required(),
            password: Joi.string().pattern(new RegExp('^[A-Z][a-z]{3,10}$')).required()
        })

        return scheme.validate(user, { abortEarly: false });
    }

    return (
        <>
            <div className=" w-75 mx-auto mt-5 pt-5">
                <h2 className='mb-4'>Registeration Form</h2>
                {validationErrors.map((error, i)=> i === 4 ? <div key={i} className='alert alert-danger' >in Valid Password </div>:<div key={i} className='alert alert-danger' >{error.message}</div>)}
                {error.length > 0 ? <div className='alert alert-danger' >{error}</div> : ''}
                <form onSubmit={submitRegisterForm}>
                    <label htmlFor="first_name">FirstName</label>
                    <input onChange={getUserData} className='form-control mt-2 mb-3' type="text" id='first_name' name='first_name' />

                    <label htmlFor="last_name">LastName</label>
                    <input onChange={getUserData} className='form-control mt-2 mb-3' type="text" id='last_name' name='last_name' />

                    <label htmlFor="age">Age</label>
                    <input onChange={getUserData} className='form-control mt-2 mb-3' type="number" id='age' name='age' />

                    <label htmlFor="email">Email</label>
                    <input onChange={getUserData} className='form-control mt-2 mb-3' type="email" id='email' name='email' />

                    <label htmlFor="password">Password</label>
                    <input onChange={getUserData} className='form-control mt-2 mb-3' type="password" id='password' name='password' />

                    <button className='btn btn-outline-info ' type='submit'>
                        {isLoading ? <i className='fas fa-spinner fa-spin'></i> : 'Register'}
                    </button>
                </form>
            </div>
        </>
    )
}
