import React from 'react'
import { useParams } from 'react-router-dom'
import { useState } from 'react';
import axios from 'axios';
import { useEffect } from 'react';
export default function MovieDetails() {
    let params = useParams();
    const [tvDatails, setTvDatails] = useState([])

    async function getTvDetails(id) {
        let { data } = await axios.get(`https://api.themoviedb.org/3/tv/${id}?api_key=eba8b9a7199efdcb0ca1f96879b83c44&language=en-US`)
        setTvDatails(data);
    }

    useEffect(() => {
        getTvDetails(params.id);
    }, [])

    return (
        <>
            {tvDatails ? <div className='container mt-5 pt-5'>
                <div className="row  g-5">
                    <div className="col-md-3">
                        <img src={`https://image.tmdb.org/t/p/w500` + tvDatails.poster_path} className='w-100' alt="" />
                    </div>
                    <div className="col-md-9">
                        <h2>{tvDatails.name}</h2>
                        <p className='text-muted py-4 h5 lh-base'>{tvDatails.overview}</p>
                        <ul className='ps-0'>
                            <li className='mb-3 h5'>Air Date : {tvDatails.last_air_date}</li>
                            <li className='mb-3 h5'>Season Number : {tvDatails.number_of_seasons}</li>
                            <li className='mb-3 h5'>popularity : {tvDatails.popularity}</li>
                            <li className='mb-3 h5'>Vote : {tvDatails.vote_average}</li>
                            <li className='h5'>Voute Count : {tvDatails.vote_count}</li>
                        </ul>
                    </div>
                </div>
            </div> :
                <div className='vh-100 w-100 d-flex justify-content-center align-items-center'>
                    <i className='fas fa-spinner fa-spin fa-3x'></i>
                </div>}

        </>
    )
}
