import axios from 'axios';
import Joi from 'joi';
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';

export default function Login(props) {
    //navigate
    let navigate = useNavigate();
    //is loading Spinner
    const [isLoading, setIsLoading] = useState(false)
    //Validation Errors
    const [validationErrors, setValidationErrors] = useState([]);
    //Registration Error Message
    const [error, setError] = useState('');
    //User Data
    const [user, setUser] = useState({
        email: '',
        password: ''
    });

    //Get user Data From Form Inputs
    function getUserData(e) {
        let myUser = { ...user };//Deep Copy
        myUser[e.target.name] = e.target.value;
        setUser(myUser);
    }

    //Submit Login Form
    async function submitLoginForm(e) {
        //stop Browser reload on submit
        e.preventDefault();
        //show loading Spinner
        setIsLoading(true);
        //Validation Result
        let validationResult = validateLoginForm();
        // if There Are Errors??
        if (validationResult.error) {
            //stop Loading Spinner
            setIsLoading(false);
            //Show Validation Error Details
            setValidationErrors(validationResult.error.details);
        }
        //if There is No Errors
        else {
            //post User Data To Data base
            let { data } = await axios.post('https://route-egypt-api.herokuapp.com/signin', user)
            //Registration tmam
            if (data.message === 'success') {
                //stop Loading Spinner
                setIsLoading(false);
                //Save Token In Local Storage
                localStorage.setItem('userToken', data.token);
                //Call SaveUserData Function From Props => (App.js)
                props.saveUserData();
                //Navigate to Login
                navigate('/home');
            }
            //Registration Error
            else {
                //stop Loading Spinner
                setIsLoading(false);
                //show Error
                setError(data.message);
            }
        }
    }

    //Validate Login Form
    function validateLoginForm() {
        let scheme = Joi.object({
            email: Joi.string().email({ minDomainSegments: 2, tlds: { allow: ['com', 'net'] } }).required(),
            password: Joi.string().pattern(new RegExp('^[A-Z][a-z]{3,10}$')).required()
        })

        return scheme.validate(user, { abortEarly: false });
    }

    return (
        <>
            <div className=" w-75 mx-auto mt-5 pt-5">
                <h2 className='mb-4'>Login Form</h2>
                {validationErrors.map((error, i)=> i === 1 ? <div key={i} className='alert alert-danger' >in Valid Password </div>:<div key={i} className='alert alert-danger' >{error.message}</div>)}
                {error.length > 0 ? <div className='alert alert-danger' >{error}</div> : ''}
                <form onSubmit={submitLoginForm}>
                    <label htmlFor="email">Email</label>
                    <input onChange={getUserData} className='form-control mt-2 mb-3' type="email" id='email' name='email' />

                    <label htmlFor="password">Password</label>
                    <input onChange={getUserData} className='form-control mt-2 mb-3' type="password" id='password' name='password' />

                    <button className='btn btn-outline-info ' type='submit'>
                        {isLoading ? <i className='fas fa-spinner fa-spin'></i> : 'Login'}
                    </button>
                </form>
            </div>
        </>
    )
}
