import React from 'react'
import { Link, NavLink } from 'react-router-dom'
export default function Navbar(props) {
    return (
        <div>
            <nav className="navbar navbar-expand-lg navbar-dark">
                <div className="container-fluid">
                    <NavLink className="navbar-brand" to='home' >Noxe</NavLink>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                            {/* If User Login */}
                            {props.userData ?
                                <>
                                    <li className="nav-item">
                                        <NavLink className={({isActive})=> isActive ? 'nav-link active' : 'nav-link'} aria-current="page" to='home' >Home</NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink className={({isActive})=> isActive ? 'nav-link active' : 'nav-link'} aria-current="page" to='movies' >Movies</NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink className={({isActive})=> isActive ? 'nav-link active' : 'nav-link'} aria-current="page" to='people' >People</NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink className={({isActive})=> isActive ? 'nav-link active' : 'nav-link'} aria-current="page" to='tv' >Tv</NavLink>
                                    </li>
                                    
                                    
                                </> : ''}
                        </ul>
                        <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
                            <li className="nav-item d-flex align-items-center order-last order-lg-first mt-2 mt-lg-0">
                                <i className='fab fa-facebook me-2'></i>
                                <i className='fab fa-spotify mx-2'></i>
                                <i className='fab fa-instagram mx-2'></i>
                                <i className='fab fa-youtube mx-2'></i>
                            </li>
                            {/* If User Login */}
                            {props.userData ?
                                <li className="nav-item">
                                    <span onClick={props.logOut} className="nav-link " aria-current="page" >Logout</span>
                                </li> :
                                <>
                                    <li className="nav-item">
                                        <Link className="nav-link " aria-current="page" to='login' >Login</Link>
                                    </li>
                                    <li className="nav-item">
                                        <Link className="nav-link " aria-current="page" to='register' >Register</Link>
                                    </li>
                                </>}
                        </ul>
                    </div>
                </div>
            </nav>
        </div>
    )
} 