import React from 'react'
import { useState } from 'react';
import axios from 'axios';
import { useEffect } from 'react';
import { Link } from 'react-router-dom';
import avatar from '../avatar.jpeg'

export default function People() {
    let nums = new Array(13).fill(1).map((elem, index) => index + 1);
    const [trendingPeople, setTrendingPeople] = useState([]);
    //Get Trending Function
    async function getTrending(pageNumber) {
        let { data } = await axios.get(`https://api.themoviedb.org/3/person/popular?api_key=39ece93b8dd373dac4034384c03dbbda&language=en-US&page=${pageNumber}`)
        setTrendingPeople(data.results);
    }
    //Call Get Trending Function
    useEffect(() => {
        getTrending(1);

    }, [])
    return (
        <>
            {trendingPeople ? <div className='container mt-5 pt-5'>
                <div className="row g-5 justify-content-center">
                    {trendingPeople.map((person, i) => <div key={i} className='col-md-2'>
                        <Link to={`/persondetails/${person.id}`}>
                            {person.profile_path === null ? <img src={avatar} className='w-100 avatar' alt="" /> :
                                <img src={"https://image.tmdb.org/t/p/w500" + person.profile_path} className='w-100 ' alt="" />
                            }
                            <h3 className='h6 my-2'>{person.name}</h3>
                        </Link>
                    </div>)}
                </div>
                <nav aria-label="...">
                    <ul className="pagination pagination-sm d-flex justify-content-center ">
                        {
                            nums.map((pageNum, i) => <li onClick={() => getTrending(pageNum)} key={pageNum} className="page-item my-4 bg-transparent"><a className="page-link bg-transparent text-white ">{pageNum}</a></li>)
                        }
                    </ul>
                </nav>
            </div> :
                <div className='vh-100 w-100 d-flex justify-content-center align-items-center'>
                    <i className='fas fa-spinner fa-spin fa-3x'></i>
                </div>}

        </>
    )
}
